.page-home {
  position: relative;
}
.page-home .list {
  width: 1200px;
}
.page-home .list__item {
  position: relative;
  padding: 16px 24px;
  margin-bottom: 10px;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  border: 1px solid #ebedf0;
  cursor: pointer;
  max-height: 240px;
  overflow: hidden;
}
.page-home .list__item:hover {
  background: #effbff;
  box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -5px 0);
          transform: translate3d(0, -5px 0);
}
.page-home .list__item:hover .list__action {
  display: block;
}
.page-home .list__item .title {
  color: #394d69;
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.2;
  cursor: pointer;
}
.page-home .list__item .title:hover {
  color: #48b2ff;
}
.page-home .list__item .description {
  cursor: pointer;
  max-height: 260px;
  overflow: hidden;
}
.page-home .list__item .create-time {
  font-size: 0.5em;
  padding-left: 20px;
}
.page-home .list__item .list-item-action {
  vertical-align: middle;
  margin-top: 10px;
  color: rgba(0, 0, 0, 0.45);
}
.page-home .list__item .list-item-action a {
  color: inherit;
}
.page-home .list__title {
  font-size: 22px;
  font-weight: 600;
}
.page-home .list__time {
  padding-left: 20px;
  font-size: 10px;
}
.page-home .list__markdown {
  opacity: 0.4;
}
.page-home .list__markdown .rc-md-editor {
  border: 0 !important;
}
.page-home .list__markdown .rc-md-editor .tool-bar {
  display: none !important;
}
.page-home .list__markdown .rc-md-editor .html-wrap {
  padding: 0 !important;
}
.page-home .list__action {
  position: absolute;
  right: 24px;
  top: 16px;
  display: none;
}
.page-home .article {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 1224px;
}
.page-home .article .ant-divider {
  margin-top: 0;
}
.page-home .article__list-item {
  cursor: pointer;
  line-height: 28px;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.page-home .article__list-item a {
  display: block;
  padding: 0 8px;
  color: #8590a6;
}
.page-home .article__list-item a:hover {
  color: #40a9ff;
  background: #effbff;
  box-shadow: 0 15px 30px 0 rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -5px 0);
          transform: translate3d(0, -5px 0);
}
.page-home .preview {
  position: fixed;
  width: 260px;
  top: 104px;
  right: 20px;
  list-style-type: circle;
}
.page-home .preview .title {
  color: #394d69;
  font-size: 22px;
}
.page-home .preview li {
  display: block;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-left: -1px;
}
.page-home .preview li a {
  color: #8590a6;
}
.page-home .preview li:hover {
  background: #f0f2f5;
}
.page-home .preview li:hover a {
  color: #40a9ff;
}
.page-home .no-data {
  margin-top: 26%;
}
.page-home .keyword {
  font-size: 18px;
  font-weight: bold;
  color: #f00;
}
