.App .content-inner-wrap {
  padding: 0px 24px 40px;
}
.App .sider-wrap {
  height: calc(100vh - 104px);
  text-align: center;
  color: rgba(0, 0, 0, 0.65);
  overflow-y: auto;
  border-right: 1px solid #ebedf0;
}
.App .main-wrap {
  background: #ffffff;
  padding: 40px 0px 0px;
}
.App .main-wrap .my-name {
  font-weight: 700;
  margin-top: 10px;
}
.App .main-wrap .my-desc {
  font-size: 13px;
  margin-bottom: 10px;
  color: rgba(0, 0, 0, 0.65);
}
.App .main-wrap .home-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.App .main-wrap .home-list a {
  padding: 0 8px;
  color: inherit;
  cursor: pointer;
}
.App .main-wrap .home-list a:hover {
  border-radius: 4px;
  background: #eee;
}
.App .main-wrap .home-list a span {
  padding-left: 5px;
}
.App .main-wrap .home-list__logo {
  width: 14px;
  height: 14px;
}
.App .main-wrap .article-list {
  text-align: left;
  margin-bottom: 14px;
}
.App .main-wrap .article-list li a {
  display: block;
  line-height: 32px;
  padding: 0 20px;
  color: #8590a6;
}
.App .main-wrap .article-list li:hover {
  background-color: #f0f2f5;
}
.App .main-wrap .ant-tag {
  margin: 0 8px 8px 0;
  cursor: pointer;
}
.App footer {
  text-align: center;
}
