.header {
  line-height: 64px;
  box-shadow: #f0f1f2 0px 2px 8px;
  position: relative;
  z-index: 10;
  max-width: 100%;
  padding: 0px;
  background: #ffffff;
}
.header__title {
  font-size: 20px;
  padding-left: 100px;
}
.header__btn {
  float: right;
}
.header__btn button {
  margin-right: 8px;
}
.header .ant-menu {
  float: right;
}
.header .ant-menu .ant-menu-item {
  line-height: 64px;
}
